import React from 'react'
import SampleItem from '../sampleItem/sampleItem.js'
import { Radio, Select, Collapse, Checkbox, Col, Row, InputNumber } from 'antd'
import NodeLinkStylePanel from '../nodeLinkStylePanel/nodeLinkStylePanel.js'
import {
    TIME_PANEL_INPUT_WIDTH as TPIW,
    TIME_TIMELINE_ELEMENT,
    KEYFRAM_OPTIONS,
    TASK_FIND_ATTR,
    TASK_FIND_RELATION,
    TASK_CHANGE_TYPES,
    PATTERN_TO_CHANGE
} from '../../util/const'
import { COMPARISON_CONFIG } from '../../util/defaultConfig.js'
import './comparisonPanel.css'
import { connect } from 'react-redux'
import { modifyConfig } from '../../redux/config.redux.js'

const { Option } = Select
const { Panel } = Collapse
// Trend或者 Diff 配置
// 下面是trend配置

const columnButtonStyle = {
    boxSizing: 'border-box',
    width: '62px',
    padding: '0px',
    fontSize: '12px',
    height: '30px',
    lineHeight: '30px',
    alighItems: 'center'
}
const rowButtonStyle = {
    boxSizing: 'border-box',
    width: '62px',
    padding: '0px',
    fontSize: '12px',
    height: '62px',
    lineHeight: '62px',
    alighItems: 'center'
}

const index2chooseItem = [
    'appear-Node',
    'stable-Node',
    'disappear-Node',
    'appear-Link',
    'stable-Link',
    'disappear-Link'
]

class TaskPanel extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            indeterminate: true,
            checkAll: false,
            changeIndeterminate: true,
            changeCheckAll: true,
            changeList: [...TASK_CHANGE_TYPES],
            taskPattern: 'graph',
            taskChange: 'all'
        }
    }
    handleOptionChange = (value) => {
        this.handleComparisonChange({ ...value })
    }
    handleSelectChange = (value, key, option) => {
        if (option === 'comparison') {
            this.handleComparisonChange({ [key]: value })
        } else if (option === 'find') {
            this.handleFindChange({ [key]: value })
        }
    }
    handleColumnChange = (e) => {
        const nodeOrLink = this.props.options.comparison.chooseItem.split('-')[1]
        this.handleComparisonChange({ chooseItem: `${e.target.value}-${nodeOrLink}` })
    }
    handleRowChange = (e) => {
        const changeAttr = this.props.options.comparison.chooseItem.split('-')[0]
        this.handleComparisonChange({ chooseItem: `${changeAttr}-${e.target.value}` })
    }
    handleIconsClick = (index) => {
        this.handleComparisonChange({ chooseItem: index2chooseItem[Number(index)] })
    }
    handleIsOnChange = (value) => {
        this.handleComparisonChange({ isOn: value })
    }

    handleCheckChange = (list) => {
        this.handleComparisonChange({ chooseTypes: list })
        this.setState({ indeterminate: !!list.length && list.length < COMPARISON_CONFIG.length })
        this.setState({ checkAll: list.length === COMPARISON_CONFIG.length })
    }
    handleAllCheckChange = (e) => {
        this.handleComparisonChange({ chooseTypes: e.target.checked ? COMPARISON_CONFIG : [] })
        this.setState({ indeterminate: false })
        this.setState({ checkAll: e.target.checked })
    }

    handleChangeCheckChange = (list) => {
        this.handleTaskPanelChange('change', list)
    }
    handleAllChangeCheckChange = (e) => {
        this.handleTaskPanelChange('change', [...TASK_CHANGE_TYPES])
    }

    handleComparisonChange = (value) => {
        this.changeTaskConfig({
            comparison: {
                ...this.props.options.comparison,
                ...value
            }
        })
    }
    handleFindChange = (value) => {
        this.changeTaskConfig({
            find: {
                ...this.props.options.find,
                ...value
            }
        })
    }

    handleTaskPanelChange = (key, value) => {
        this.changeTaskConfig({
            [key]: value
        })
    }

    handleTaskPatternSelect = (value) => {
        switch (value) {
            case 'graph':
                this.handleTaskPanelChange('taskType', 'none')
                this.handleTaskPanelChange('pattern', 'graph')
                this.handleTaskPanelChange('change', [...PATTERN_TO_CHANGE['graph']])
                break
            case 'compare-structure':
                this.handleTaskPanelChange('taskType', 'comparison')
                this.handleTaskPanelChange('basedType', 'structure')
                this.handleTaskPanelChange('pattern', 'compare-structure')
                this.handleTaskPanelChange('change', [...PATTERN_TO_CHANGE['compare-structure']])
                break
            case 'shortest-path':
                this.handleTaskPanelChange('taskType', 'find')
                this.handleTaskPanelChange('basedType', 'structure')
                this.handleTaskPanelChange('pattern', 'shortest-path')
                this.handleTaskPanelChange('change', [...PATTERN_TO_CHANGE['shortest-path']])
                break
            case 'dumb-bell':
                this.handleTaskPanelChange('taskType', 'find')
                this.handleTaskPanelChange('basedType', 'structure')
                this.handleSelectChange('dumb-bell', 'structure', 'find')
                this.handleTaskPanelChange('pattern', 'dumb-bell')
                this.handleTaskPanelChange('change', [...PATTERN_TO_CHANGE['dumb-bell']])
                break
            case 'compare-degree':
                this.handleTaskPanelChange('taskType', 'comparison')
                this.handleTaskPanelChange('basedType', 'attr')
                this.handleTaskPanelChange('pattern', 'compare-degree')
                this.handleTaskPanelChange('change', [...PATTERN_TO_CHANGE['compare-degree']])
                break
            case 'find-degree':
                this.handleTaskPanelChange('taskType', 'find')
                this.handleTaskPanelChange('basedType', 'attr')
                this.handleTaskPanelChange('pattern', 'find-degree')
                this.handleTaskPanelChange('change', [...PATTERN_TO_CHANGE['find-degree']])
                break
            default:
                this.handleTaskPanelChange('taskType', 'none')
                this.handleTaskPanelChange('pattern', 'graph')
                this.handleTaskPanelChange('change', [...PATTERN_TO_CHANGE['graph']])
        }
    }
    handleTaskChangeSelect = (value) => {
        this.setState({
            taskChange: value
        })
    }
    changeTaskConfig = (value) => {
        this.props.modifyConfig({ key: 'task', value })
    }

    render() {
        const { comparison, find, taskType, basedType, pattern, change} = this.props.options
        const {
            chooseItem,
            keyFrame,
            elements,
            chooseTypes,
            appearNode,
            appearLink,
            stableNode,
            stableLink,
            disappearNode,
            disappearLink
        } = comparison
        let changeKey = chooseItem.split('-')
        changeKey = changeKey.join('')
        const changeOptions = comparison[changeKey]
        return (
            <div className="Comparison-box combine-inner-border">
                <div className="combine-inner-title">&nbsp;Task</div>
                <div className="encoding-table-container">
                    <div className="change-option-item">
                        <div>TaskType</div>
                        <Select
                            value={taskType}
                            size="small"
                            style={{ width: TPIW }}
                            onChange={(value) => this.handleTaskPanelChange('taskType', value)}
                        >
                            <Option value="comparison">comparison</Option>
                            <Option value="find">find</Option>
                            <Option value="none">none</Option>
                        </Select>
                    </div>
                    <div className="change-option-item">
                        <div >BasedType</div>
                        <Select
                            value={basedType}
                            size="small"
                            style={{ width: TPIW }}
                            onChange={(value) => this.handleTaskPanelChange('basedType', value)}
                        >
                            <Option value="attr">attr</Option>
                            <Option value="structure">structure</Option>
                        </Select>
                    </div>
                    <div className="change-option-item">
                        <Collapse
                            expandIconPosition={'right'}
                            size="small"
                            style={{
                                backgroundColor: '#ffffff',
                                width: '100%'
                            }}
                            defaultActiveKey={['1']}
                        >
                            <Panel key="1" className="nlsp-panel" header={'Change'}>
                                <Col
                                    style={{
                                        width: '50%',
                                        display: 'flex',
                                        flexDirection: 'flex-start',
                                        paddingLeft: '10px'
                                    }}
                                >
                                    <Checkbox
                                        indeterminate={false}
                                        onChange={this.handleAllChangeCheckChange}
                                        checked={
                                            change.length === TASK_CHANGE_TYPES.length
                                        }
                                    >
                                        all
                                    </Checkbox>
                                </Col>
                                <Checkbox.Group
                                    style={{
                                        textAlign: 'left',
                                        width: '100%',
                                        paddingLeft: '10px'
                                    }}
                                    value={change}
                                    onChange={this.handleChangeCheckChange}
                                >
                                    {[0, 2, 4].map((v) => {
                                        return (
                                            <Row
                                                style={{
                                                    width: '100%'
                                                }}
                                                key={`row-${v}`}
                                            >
                                                <Col
                                                    style={{
                                                        width: '50%',
                                                        display: 'flex',
                                                        flexDirection: 'flex-start'
                                                    }}
                                                >
                                                    <Checkbox value={TASK_CHANGE_TYPES[v]}>
                                                        {TASK_CHANGE_TYPES[v]}
                                                    </Checkbox>
                                                </Col>
                                                <Col
                                                    style={{
                                                        width: '50%',
                                                        display: 'flex',
                                                        flexDirection: 'flex-start'
                                                    }}
                                                >
                                                    <Checkbox
                                                        style={{ fontSize: '10px' }}
                                                        value={TASK_CHANGE_TYPES[v + 1]}
                                                    >
                                                        {TASK_CHANGE_TYPES[v + 1]}
                                                    </Checkbox>
                                                </Col>
                                            </Row>
                                        )
                                    })}
                                </Checkbox.Group>
                            </Panel>
                        </Collapse>
                    </div>
                    {pattern === 'shortest-path' ? (
                        <>
                            <div className="change-option-item">
                                <div>Start:</div>
                                <Select
                                    value={'A'}
                                    style={{ width: TPIW }}
                                    size="small"
                                >
                                    <Option key={'A'} value={'A'}>
                                        {'A'}
                                    </Option>
                                </Select>
                            </div>
                            <div className="change-option-item">
                                <div>End:</div>
                                <Select
                                    value={'F'}
                                    style={{ width: TPIW }}
                                    size="small"
                                >
                                    <Option key={'F'} value={'F'}>
                                        {'F'}
                                    </Option>
                                </Select>
                            </div>
                        </>
                    ) : null}
                    {pattern === 'find-degree' ? (
                        <>
                            <div className="change-option-item">
                                <div>Relation:</div>
                                <Select
                                    value={find.relation}
                                    style={{ width: TPIW }}
                                    size="small"
                                    onChange={(value) =>
                                        this.handleSelectChange(value, 'relation', 'find')
                                    }
                                >
                                    {TASK_FIND_RELATION.map((v) => {
                                        return (
                                            <Option key={v} value={v}>
                                                {v}
                                            </Option>
                                        )
                                    })}
                                </Select>
                            </div>
                            <div className="change-option-item">
                                <div>Value:</div>
                                <InputNumber
                                    size="small"
                                    min={0}
                                    max={100}
                                    value={find.value}
                                    onChange={(e) => this.handleFindChange({ value: Number(e) })}
                                    style={{ width: TPIW }}
                                />
                            </div>
                        </>
                    ) : null}
                    <div className="change-option-item">
                        <Collapse
                            expandIconPosition={'right'}
                            style={{
                                backgroundColor: '#ffffff',
                                width: '100%'
                            }}
                            size="small"
                            defaultActiveKey={['0']}
                        >
                            <Panel key="1" className="nlsp-panel" header={'chooseTypes'}>
                                <Col
                                    style={{
                                        width: '50%',
                                        display: 'flex',
                                        flexDirection: 'flex-start',
                                        paddingLeft: '10px'
                                    }}
                                >
                                    <Checkbox
                                        indeterminate={this.state.indeterminate}
                                        onChange={this.handleAllCheckChange}
                                        checked={this.state.checkAll}
                                    >
                                        all
                                    </Checkbox>
                                </Col>
                                <Checkbox.Group
                                    style={{
                                        textAlign: 'left',
                                        width: '100%',
                                        paddingLeft: '10px'
                                    }}
                                    value={chooseTypes}
                                    onChange={this.handleCheckChange}
                                >
                                    {[0, 2, 4, 6].map((v) => {
                                        return (
                                            <Row
                                                style={{
                                                    width: '100%'
                                                }}
                                                key={`row-${v}`}
                                            >
                                                <Col
                                                    style={{
                                                        width: '55%',
                                                        display: 'flex',
                                                        flexDirection: 'flex-start'
                                                    }}
                                                >
                                                    <Checkbox value={COMPARISON_CONFIG[v]}>
                                                        {COMPARISON_CONFIG[v]}
                                                    </Checkbox>
                                                </Col>
                                                <Col
                                                    style={{
                                                        width: '45%',
                                                        display: 'flex',
                                                        flexDirection: 'flex-start'
                                                    }}
                                                >
                                                    <Checkbox value={COMPARISON_CONFIG[v + 1]}>
                                                        {COMPARISON_CONFIG[v + 1]}
                                                    </Checkbox>
                                                </Col>
                                            </Row>
                                        )
                                    })}
                                </Checkbox.Group>
                            </Panel>
                        </Collapse>
                    </div>    
                    {taskType === 'comparison' ? (
                        <>
                            <div className="change-option-item">
                                <div>KeyFrame:</div>
                                <Select
                                    value={keyFrame}
                                    style={{ width: TPIW }}
                                    size="small"
                                    onChange={(value) =>
                                        this.handleSelectChange(value, 'keyFrame', 'comparison')
                                    }
                                >
                                    {KEYFRAM_OPTIONS.map((v) => {
                                        return (
                                            <Option key={v} value={v}>
                                                {v}
                                            </Option>
                                        )
                                    })}
                                </Select>
                            </div>
                            <div className="change-option-item">
                                <div>Elements:</div>
                                <Select
                                    value={elements}
                                    style={{ width: TPIW }}
                                    size="small"
                                    onChange={(value) =>
                                        this.handleSelectChange(value, 'elements', 'comparison')
                                    }
                                >
                                    {TIME_TIMELINE_ELEMENT.map((v) => {
                                        return (
                                            <Option key={v} value={v}>
                                                {v}
                                            </Option>
                                        )
                                    })}
                                </Select>
                            </div>
                        </>
                    ) : null}
                    {taskType === 'find' && basedType === 'attr' ? (
                        <>
                            <div className="change-option-item">
                                <div>Attr:</div>
                                <Select
                                    value={find.attr}
                                    style={{ width: TPIW }}
                                    size="small"
                                    onChange={(value) =>
                                        this.handleSelectChange(value, 'attr', 'find')
                                    }
                                >
                                    {TASK_FIND_ATTR.map((v) => {
                                        return (
                                            <Option key={v} value={v}>
                                                {v}
                                            </Option>
                                        )
                                    })}
                                </Select>
                            </div>
                            <div className="change-option-item">
                                <div>Relation:</div>
                                <Select
                                    value={find.relation}
                                    style={{ width: TPIW }}
                                    size="small"
                                    onChange={(value) =>
                                        this.handleSelectChange(value, 'relation', 'find')
                                    }
                                >
                                    {TASK_FIND_RELATION.map((v) => {
                                        return (
                                            <Option key={v} value={v}>
                                                {v}
                                            </Option>
                                        )
                                    })}
                                </Select>
                            </div>
                            <div className="change-option-item">
                                <div>Value:</div>
                                <InputNumber
                                    size="small"
                                    min={0}
                                    max={100}
                                    value={find.value}
                                    onChange={(e) => this.handleFindChange({ value: Number(e) })}
                                    style={{ width: TPIW }}
                                />
                            </div>
                        </>
                    ) : null}

                    <div className="comparison-table-container">
                        <div className="table-first-line">
                            <div className="blank-icon"></div>
                            <Radio.Group
                                onChange={this.handleColumnChange}
                                value={chooseItem.split('-')[0]}
                                className="first-line-right"
                            >
                                <Radio.Button style={columnButtonStyle} value="appear">
                                    appear
                                </Radio.Button>
                                <Radio.Button style={columnButtonStyle} value="stable">
                                    stable
                                </Radio.Button>
                                <Radio.Button style={columnButtonStyle} value="disappear">
                                    disappear
                                </Radio.Button>
                            </Radio.Group>
                        </div>
                        <div className="table-second-line">
                            <div className="second-line-left">
                                <Radio.Group
                                    onChange={this.handleRowChange}
                                    value={chooseItem.split('-')[1]}
                                >
                                    <Radio.Button style={rowButtonStyle} value="Node">
                                        Node
                                    </Radio.Button>
                                    <Radio.Button style={rowButtonStyle} value="Link">
                                        Link
                                    </Radio.Button>
                                </Radio.Group>
                            </div>
                            <div className="second-line-right">
                                <div
                                    id="appear-Node"
                                    onClick={() => this.handleIconsClick(0)}
                                    className={`line-icon-container ${
                                        chooseItem === 'appear-Node' ? 'choose-icon' : ''
                                    }`}
                                >
                                    <SampleItem config={appearNode} />
                                </div>
                                <div
                                    onClick={() => this.handleIconsClick(1)}
                                    className={`line-icon-container ${
                                        chooseItem === 'stable-Node' ? 'choose-icon' : ''
                                    }`}
                                >
                                    <SampleItem config={stableNode} />
                                </div>
                                <div
                                    className={`line-icon-container ${
                                        chooseItem === 'disappear-Node' ? 'choose-icon' : ''
                                    }`}
                                    onClick={() => this.handleIconsClick(2)}
                                >
                                    <SampleItem config={disappearNode} />
                                </div>
                                <div
                                    onClick={() => this.handleIconsClick(3)}
                                    className={`line-icon-container ${
                                        chooseItem === 'appear-Link' ? 'choose-icon' : ''
                                    }`}
                                >
                                    <SampleItem config={appearLink} type={'link'} />
                                </div>
                                <div
                                    onClick={() => this.handleIconsClick(4)}
                                    className={`line-icon-container ${
                                        chooseItem === 'stable-Link' ? 'choose-icon' : ''
                                    }`}
                                >
                                    <SampleItem config={stableLink} type={'link'} />
                                </div>
                                <div
                                    onClick={() => this.handleIconsClick(5)}
                                    className={`line-icon-container ${
                                        chooseItem === 'disappear-Link' ? 'choose-icon' : ''
                                    }`}
                                >
                                    <SampleItem config={disappearLink} type={'link'} />
                                </div>
                            </div>
                        </div>
                    </div>
                    <NodeLinkStylePanel
                        type={chooseItem.split('-')[1]}
                        optionKey={chooseItem.split('-').join('')}
                        changeOptions={changeOptions}
                        onSubmit={this.handleComparisonChange}
                    />
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    options: state.config.task
})

const mapDispatchToProps = {
    modifyConfig
}

export default connect(mapStateToProps, mapDispatchToProps)(TaskPanel)
